import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { Button, Modal, ModalHeader, ModalBody, FormGroup, Label } from 'reactstrap'
import {Formik, Field, Form, ErrorMessage} from 'formik'
import {validateCommentForm} from '../../../../utilities/validateCommentForm'
import { addComment } from './commentsSlice';


const CommentForm = ({ campsiteId }) => {
   const [modalOpen, setModalOpen] = useState(false);
   console.log({ campsiteId });

   const dispatch = useDispatch();
   const handleSubmit = (values) => {
      const comment = {
         campsiteId:parseInt(campsiteId),
         rating: values.rating,
         author: values.author,
         text: values.commentText,
         date: new Date(Date.now()).toISOString()
      };
     console.log(`Normal form returned: ${comment}`);
     // console.log(`JSON FORMAT:`,JSON.stringify(comment));
      dispatch(addComment(comment));
      setModalOpen(false)
   }
   return (
      <>
         <Button outline onClick={() => setModalOpen(true)}>
            <i className='fa fa-pencil fa-lg' /> Add Comment

         </Button>

         {/* Modal */}

         <Modal isOpen={modalOpen}>
            <ModalHeader toggle={() => setModalOpen(false)}>
               Add Comment
            </ModalHeader>


            {/* Modal Body */}
            {/* Formik form below */}

            <ModalBody>

               <Formik initialValues={{
                  rating:undefined,
                  author:'',
                  commentText:''
               }} onSubmit={handleSubmit}
               validate={validateCommentForm}
               >
                  
                  <Form>
                     <FormGroup>
                        <Label htmlFor='rating'>
                           Rating
                        </Label>
                        <Field
                                    name='rating'
                                    as='select'
                                    className='form-control'
                                >
                                    <option>Select...</option>
                                    <option>1</option>
                                    <option>2</option>
                                    <option>3</option>
                                    <option>4</option>
                                    <option>5</option>
                        </Field>
                        <ErrorMessage name='rating'>
                     {(msg) => <div className='text-danger'>{msg}</div>}
                  </ErrorMessage>
                     </FormGroup>

                     <FormGroup>
                        <Label htmlFor='author'>
                           Your Name
                        </Label>
                        <Field
                                    name='author'
                                    placeholder='Your Name'
                                    className='form-control'
                        />
                         <ErrorMessage name='author'>
                     {(msg) => <span className='text-danger'>{msg}</span>}
                  </ErrorMessage>
                     </FormGroup>

                     <FormGroup>
                        <Label htmlFor='commentText'>
                        Comment (<span style={{ fontStyle: 'italic', color:'rgb(252, 63, 0)' }}>REQUIRED</span>)
                        </Label>
                        <Field
                                    name='commentText'
                                    as='textarea'
                                    rows='12'
                                    className='form-control'
                        />
                        <ErrorMessage name='commentText'>
                     {(msg) => <span className='text-danger'>{msg}</span>}
                        </ErrorMessage>
                     </FormGroup>

                     <Button type='submit' color='primary'>
                                Submit
                     </Button>
                     
                  </Form>

               </Formik>

            </ModalBody>
         </Modal>
      </>
   )
}

export default CommentForm